import React, { Component } from "react"
import Form from "react-bootstrap/Form"
import Alert from "react-bootstrap/Alert"
import axios from "axios"
import Button from "react-bootstrap/Button"
import { Spinner } from "react-bootstrap"
import posed from "react-pose"
const FadeDiv = posed.div({
  visible: { opacity: 1 },
  hidden: { opacity: 0 },
})
const emailRe = /[^@]+@[^@]+\.[^@]+/
const styles = {
  center: { display: "flex", justifyContent: "center", alignItems: "center" },
}
const INIT_STATE = {
  name: "",
  email: "",
  message: "",
  error: null,
  submitted: false,
  sending: false,
  sent: false,
}
class ContactForm extends Component {
  constructor(props) {
    super(props)
    this.state = { ...INIT_STATE }
  }
  onSubmit = event => {
    this.setState({ submitted: true })
    const isInvalid =
      !this.state.name || !emailRe.test(this.state.email) || !this.state.message
    if (!isInvalid) {
      const dest = "https://formspree.io/f/xnqonepp"
      const data = this.state
      this.setState({ sending: true })
      axios
        .post(dest, { data })
        .then(res => {
          this.setState({ ...INIT_STATE, sent: !this.state.sent })
          setTimeout(() => {
            this.setState({ sent: !this.state.sent })
          }, 3200)
        })
        .catch(error => {
          this.setState({ error, sending: false })
        })
    }
  }
  onChange = event => {
    this.setState({ [event.target.name]: event.target.value })
  }
  render() {
    return (
      <div style={styles.center}>
        <Form style={{ maxWidth: 600, width: "70%" }}>
          <Form.Group controlId="name">
            <Form.Label>Your Name</Form.Label>
            <Form.Control
              name="name"
              placeholder="Name"
              value={this.state.name}
              isInvalid={!this.state.name && this.state.submitted}
              onChange={this.onChange}
            />
            <Form.Control.Feedback type="invalid">
              Please enter a name.
            </Form.Control.Feedback>
          </Form.Group>
          <Form.Group controlId="email">
            <Form.Label>Your Email</Form.Label>
            <Form.Control
              name="email"
              type="email"
              isInvalid={
                !emailRe.test(this.state.email) && this.state.submitted
              }
              placeholder="Email"
              value={this.state.email}
              onChange={this.onChange}
            />
            <Form.Control.Feedback type="invalid">
              Please enter a valid email.
            </Form.Control.Feedback>
          </Form.Group>
          <Form.Group controlId="message">
            <Form.Label>Message</Form.Label>
            <Form.Control
              name="message"
              as="textarea"
              placeholder="Message"
              isInvalid={this.state.submitted && !this.state.message}
              value={this.state.message}
              onChange={this.onChange}
            />
            <Form.Control.Feedback type="invalid">
              Password must be at least 6 letters.
            </Form.Control.Feedback>
          </Form.Group>
          <Button onClick={this.onSubmit} style={{ width: 90 }}>
            {this.state.sending ? (
              <Spinner animation="border" role="status" size="sm" />
            ) : (
              <div>Send</div>
            )}
          </Button>
          {this.state.error && (
            <Alert variant="danger" style={{ margin: 15 }}>
              {this.state.error.message}
            </Alert>
          )}

          <FadeDiv
            style={styles.center}
            pose={this.state.sent ? "visible" : "hidden"}
          >
            <h3
              style={{ marginTop: 50, textAlign: "center", ...styles.center }}
            >
              Thanks, we'll be in touch shortly!
            </h3>
          </FadeDiv>
        </Form>
      </div>
    )
  }
}

export default ContactForm
